.github-container{
    margin: 0px;
    width: 90vw;
    max-width: 1200px;

    border: 0.15vw;
    border-top: 0.075vw;
    border-bottom: 0.075vw;
    border-color: black;
    border-style: solid;

    display: flex;
}

.github-profile{
    max-width: fit-content;
    margin-left: 30px;
    margin-right: 35px;

    background-color: #0D1117;
    color: #f5f5f5;


    text-align: center;

    border-radius: 1rem;

    box-shadow: 1vh 1vh rgb(46, 46, 46);

    display: flex;
    flex-direction: column;

    font-family: Helvetica, sans-serif;
    text-decoration: none;
}

.github-picture{
    border-radius: 50%;
    border-color: white;
    border: 5px solid;
    width: 30vw;
    max-width: 300px;

    margin: 20px;
}

.sub-container{
    margin: auto;
    margin-top: 3vh;
    margin-bottom: 3vh;

}


.svg-filter{
    margin: auto;
    margin-top: 10px;
    width: 5vw;
    max-width: 50px;
    filter: invert(100%) sepia(99%) saturate(2%) hue-rotate(237deg) brightness(104%) contrast(100%);
}


.github-label{
    font-size: clamp(15px, 2vw, 30px);
    margin-bottom: 10px;
}

.github-button{
    text-decoration: none;
    color: white;

    border-radius: 0.3rem;
    margin: auto;
    margin-bottom: 2vh;
    margin-top: 1vh;
    
    padding-top: 1vh;
    padding-bottom: 1vh;
    padding-left: 1vw;
    padding-right: 1vw;
    width: fit-content;
    background-color: rgb(54, 123, 183);

    box-shadow: 0.5vh 0.5vh rgb(39, 90, 133);

    font-size: clamp(5px, 2vw, 15px);

    transition: all 200ms ease-in-out;
}


.github-button:hover {
    transform: translate(-5px, -5px);

    cursor: pointer;
}