.nav-container{
    margin: 0px;
    max-height: 200px;
    width: 90vw;
    max-width: 1200px;

    border: 0.15vw;
    
    border-color: black;
    border-style: solid;
    
}

.nav-items{
    font-family: 'PostBus';
    font-size: clamp(12px, 1.5vw, 30px);
    margin: 0px;

    padding:0px;


    display: flex;
    flex-direction: row;
    justify-content: space-around;

    list-style-type: none;
}

.nav-individual{
    padding-top: 7px;
    padding-bottom: 5px;
    text-align: center;
    width: 22.5vw;
    border: 0px;
    border-right: 0.15vw;
    border-color: black;
    border-style: solid;

    transition: all 150ms ease-in-out;
}

.nav-individual:hover{

    border-right: 0.15vw;
    border-color: black;
    border-style: solid;
    background-color: black;
    color: #d6d4c5;
    transform: translate(5px, 5px);
    border: 0px;
    cursor: pointer;

}

.nav-individual:last-child{
    border: 0px;
}

.scroll-link{
    display: block;
    margin: 0px;
    padding: 0px;
    min-width: 100%;
}