.img-container{
    margin: 0px;
    width: 90vw;
    max-width: 1200px;

    border: 0.15vw;
    border-top: 0.075vw;
    border-bottom: 0.075vw;
    border-color: black;
    border-style: solid;

    font-family: 'Courier New', Courier, monospace;
    text-align: center;
}

.img-items{
    display:flex;
    align-items: center;
    justify-content: space-between;
}

.img-style{
    margin-top: 3vh;
    margin-bottom: 4vh;


    width: 40vw;
    max-width: 500px;

    box-shadow: 1vh 1vh;
    transition: all 200ms ease-in-out;
}

.img-left{
    margin-right: 3vw;
}

.img-right{
    margin-left: 2vw;
}

.img-style:hover{
    transform: translate(-5px, -5px);
}

.img-nav-bar{
    margin: auto;

    width: 40vw;
    max-width: 500px;

    
    border: 0.15vw;
    border-color: black;
    border-style: solid;    
}

.img-title{
    height: 20vh;

    font-size: clamp(15px, 2vw, 35px);
    
    padding-left: 5px;
    padding-right: 5px;

    max-height: 80px;

    display: flex;
    align-items: center;
    justify-content: center;

    border: 0.15vw;
    border-top: 0px;
    border-left: 0px;
    border-right: 0px;
    border-color: black;
    border-style: solid; 
}

.img-download{
    color: black;
    text-decoration: none;
    display: block  ;
    transition: all 200ms ease-in-out;

    font-size: clamp(5px, 2vw, 20px);
}

.img-download:hover{
    background-color: black;
    transform: translate(5px, 5px);
    color: #d6d4c5;
    cursor: pointer;
}