.container{
    margin: 0;
    margin-top: 1vh;

    display: flex;
    justify-content: center;
    height: 15vw;
    width: 90vw;
    max-width: 1200px;
    max-height: 200px;

    border: 0.15vw;
    border-bottom: 0px;
    border-color: black;
    border-style: solid;

}
.Logo{
    width: 15vw;
    height: 15vw;
    max-height: 200px;
    max-width: 200px;
    border: 0.15vw;

}

.middle{
    width: 60vw;
    max-width: 800px;

    text-align: center;
    overflow: hidden;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    
    border: 0px;
    border-left: 0.15vw;
    border-right: 0.15vw;
    border-color: black;
    border-style: solid;

    font-family: 'PostBus';
}
.date{


    font-size: clamp(5px, 2vw, 30px);
    letter-spacing: 0.25vw;
}

.name{
    margin-top: 0.5vw;
    margin-bottom: 0.5vw;

    font-size: clamp(20px, 5vw, 60px);
    letter-spacing: 0.5vw;
}

.logo-img{
    width: 15vw;
    max-width: 200px;
}