.contact-container{
    margin: 0px;
    width: 90vw;
    max-width: 1200px;

    border: 0.15vw;
    border-top: 0.075vw;
    border-color: black;
    border-style: solid;
    margin-bottom: 50px;
}

.info-box{
    width: fit-content;
    margin: auto;

    margin-top: 2vh;
    margin-bottom: 2vh;

    text-align: center;
    font-family: 'Courier New', Courier, monospace;
    color: black;
}

.information{

    text-decoration: none;
    display: block;
    
    padding: 10px;

    font-size: clamp(15px, 2vw, 30px);
    color: black;
    transition: all 250ms ease-in-out;
}

.information:hover{
    color: #d6d4c5;
    background-color: black;
}

.info-title{
    font-size: clamp(30px, 5vw, 50px);
    padding-bottom: 10px;
}