.home-container{
    margin: 0px;
    width: 90vw;
    max-width: 1200px;

    border: 0.15vw;
    border-top: 0px;
    border-bottom: 0.075vw;
    border-color: black;
    border-style: solid;

    font-family: 'Courier New', Courier, monospace;
    font-size: clamp(15px, 3vw, 40px);
}

.intro-text{
    padding-top: 2vw;
    padding-bottom: 2vw;
    padding-right: 2vh;
    padding-left: 2vh;

    text-align: center;
}