body {
  background-color: #d6d4c5;
  width: auto; 
  margin: 0 auto;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
@font-face {
  font-family: "PostBus";
  src: url("./font/postbuspro-webfont.eot");
  src: url("./font/postbuspro-webfont.woff") format("woff"),
  url("./font/postbuspro-webfont.ttf") format("truetype"),
  url("./font/postbuspro-webfont.svg") format("svg");
}

::-webkit-scrollbar {
  width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  background-color: #646359;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #a5a393;
  border-radius: 10px;
}